* {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

body {
  padding: 3rem;
}

input {
  color: brown;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  display: flex;
  flex-direction: column;
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  justify-content: center;
  margin: 0 10px 10px 0;
  padding: 1rem;
  overflow: hidden;
}

.card p {
  padding: 1rem;
  text-align: center;
}

.card img {
  max-height: 200px;
}